.site-list__item {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}

.site-list__item a {
  text-decoration: none;
  color: #000000;

  @media (min-width: $desktop-width) {
    color: #ffffff;
  }
}
